<template>
    <div>


        <van-action-sheet v-model="showLuckDetail" title="详情">
            <luck-detail :id="luck_id"></luck-detail>
        </van-action-sheet>

        <van-action-sheet v-model="showLuckCreate" title="发起红包">
            <luck-create></luck-create>
        </van-action-sheet>

        <van-action-sheet v-model="showLuckRule" title="玩法规则">
            <luck-rule></luck-rule>
        </van-action-sheet>

        <van-tabs v-model="active">
            <van-tab :title="index" v-for="index in tabs" :key="index">

                <grace-animation effect="bounceIn">
                <van-row type="flex" justify="center" class="j-ta-c j-bc-white j-p-tb-S">
                        <van-col span="7">

                            <van-tag plain round size="large" type="success" @click="showLuckCreate = true">
                                发起红包
                            </van-tag>

                        </van-col>
                        <van-col span="7">
                            <van-tag plain round size="large" type="danger">我的红包</van-tag>
                        </van-col>
                        <van-col span="7">
                            <van-tag plain round size="large" type="primary" @click="showLuckRule = true">玩法规则</van-tag>
                        </van-col>
                    </van-row>
                </grace-animation>

                <van-pull-refresh v-model="isLoading" @refresh="onRefresh">

                    <van-list v-model="loading" @load="onLoad"
                              :finished="finished" finished-text="没有更多了"
                              :error.sync="error" error-text="请求失败，点击重新加载">
                        <van-cell v-for="item in list" :key="item.id" @click="onShowLuckDetail(item.id)">

                            parseInt(item.progress){{typeof parseInt(item.progress)}} <br>
                            currentRate2{{currentRate2}} <br>
                            rate{{rate}} <br>

                            <van-row type="flex" justify="center" align="center">
                                <van-col span="6" class="j-p-r-S j-ta-r">

                                    <van-circle
                                        v-model="currentRate2"
                                        color="#ff1744"
                                        fill="#1de9b6"
                                        :rate="rate"
                                        size="70px"
                                        layer-color="#ebedf0"
                                        :speed="10"
                                        :stroke-width="60"
                                        :text="parseInt(currentRate2.toFixed(0) / (100 / item.total_count)) + '/' + item.total_count + '人'">
                                    </van-circle>

                                </van-col>
                                <van-col span="18">
                                    <div>ID{{item.id}} {{item.title}}</div>
                                    <span style="font-size: 24px;">{{item.amount}}.00</span>元参与, 一起瓜分
                                    <span style="font-size: 24px;">{{item.total_amount}}.00</span>元

                                </van-col>
                            </van-row>

                        </van-cell>
                    </van-list>

                </van-pull-refresh>

            </van-tab>
        </van-tabs>

        <tabbar></tabbar>

    </div>
</template>

<script>
  import LuckDetail from '@/components/LuckDetail.vue'
  import LuckCreate from '@/components/LuckCreate.vue'
  import LuckRule from '@/components/LuckRule.vue'

  export default {
    components: {
      LuckDetail,
      LuckCreate,
      LuckRule,
    },
    data() {
      return {


        showLuckDetail: false,
        showLuckCreate: false,
        showLuckRule: false,

        luck_id: null,

        item: '',

        isLoading: false,

        list: [],
        loading: false,
        finished: false,
        error: false,

        total: 5, // 总需人数
        currentRate2: 0,
        rate: 50, // 当前人数

        active: 2,
        tabs: ['最新', '收益高', '投入小', '可上车']
      }
    },
    created() {
      console.log("luck 实例(created)已载入")

    },
    methods: {

      onRefresh() {
        setTimeout(() => {
          this.onLoad(true)
          this.isLoading = false;
        }, 500)
      },
      onShowLuckDetail(luck_id) {
        this.luck_id = luck_id
        this.showLuckDetail = !this.showLuckDetail
        this.$router.push('?luck_id=' + luck_id)
      },
      onLoad(isRefresh = false) {

        // 异步更新数据
        setTimeout(() => {
          if (isRefresh) this.list = []

          for (let i = 0; i < 10; i++) {

            let total_count = Math.floor(Math.random() * 10) + 2
            let amount = Math.floor(Math.random() * 10)
            let current_count = 2


            let list = [{
              id: this.list.length + 1,
              title: '用户' + Math.floor(Math.random() * 1000000) + '的红包',
              total_count: total_count,
              amount: amount,
              total_amount: amount * total_count,
              current_count: current_count,
              progress: ((current_count / total_count) * 100).toFixed(0),
            }]
            this.list.push.apply(this.list, list)
          }
          // 加载状态结束
          this.loading = false;

          // 数据全部加载完成
          if (this.list.length >= 40) {
            this.finished = true;
          }
        }, 300)
      },
    },
  }
</script>
