<template>
    <div class="j-ta-c j-p-b-XXL" style="background-color: #fbe9e7;">

        <mu-container style="width: 80%; max-width: 384px;">

            <mu-text-field color="secondary" v-model="form.title" label="主题" label-float max-length="10"
                           full-width></mu-text-field>

            <mu-select color="secondary" label="总需人数" label-float v-model="form.total_count" full-width>
                <mu-option v-for="option in options" :key="option" :label="option + '人'" :value="option"></mu-option>
            </mu-select>

            <mu-text-field color="secondary" input-position="right" v-model="form.amount" label="每人支付" suffix="元"
                           onkeyup="clearNoNum(this)" label-float full-width></mu-text-field>

            <mu-flex align-items="center">
                <mu-flex justify-content="start">
                    <div class="j-c-gray-M">在大厅展示</div>
                </mu-flex>
                <mu-flex justify-content="end" fill>
                    <van-switch v-model="form.is_public" size="20px"/>
                </mu-flex>
            </mu-flex>

        </mu-container>

        <div class="j-m-tb-M">
            <mu-button color="secondary">发起并参与红包</mu-button>
        </div>

        <mu-checkbox label="同意用户协议" v-model="agree"></mu-checkbox>

    </div>
</template>

<script>

  export default {
    props: {},
    data() {
      return {

        options: [
          '2', '3', '4', '5', '6', '7', '8', '9',
        ],

        form: {
          title: null,
          total_count: null,
          amount: null,
          is_public: true,
        },

        agree: true, // 同意服务协议

        checked: false,

        temp: null,

        isLoading: false,

        detailShow: false,

        list: [],
        loading: false,
        finished: false,
        error: false,

        total: 5, // 总需人数
        currentRate2: 0,
        rate: 100, // 当前人数

        active: 2,
        tabs: ['最新', '收益高', '投入小', '可上车']
      }
    },
    created() {

    },
    methods: {
      onRefresh() {
        setTimeout(() => {
          this.$toast('刷新成功');
          this.isLoading = false;
        }, 500);
      },
      detail() {
        this.detailShow = !this.detailShow
      },
      onLoad() {
        // 异步更新数据
        setTimeout(() => {
          for (let i = 0; i < 10; i++) {
            this.list.push(this.list.length + 1);
          }
          // 加载状态结束
          this.loading = false;

          // 数据全部加载完成
          if (this.list.length >= 40) {
            this.finished = true;
          }
        }, 500);
      },
    },
  }
</script>
